/* Garden Theme Base Styles */
body {
  font-family: 'Segoe UI', Arial, sans-serif;
  background: linear-gradient(135deg, #e0ffe0 0%, #b2f7b2 100%);
  margin: 0;
  padding: 0;
  color: #3d5229;
  overflow-x: hidden;
  min-height: 100vh;
}

.app {
  text-align: center;
  padding: 30px 10px 10px 10px;
  box-sizing: border-box;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Navigation Styles */
.nav {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
  background: #e0ffe0;
  border-bottom: 2px solid #a3d977;
  padding: 1rem 0 0.5rem 0;
  margin-bottom: 1.5rem;
  border-radius: 0 0 18px 18px;
}

.nav a {
  color: #388e3c;
  font-weight: bold;
  text-decoration: none;
  font-size: 1.1rem;
  padding: 0.3em 0.8em;
  border-radius: 8px;
  transition: background 0.2s, color 0.2s;
}

.nav a:hover, .nav a.active {
  background: #b2f7b2;
  color: #2e4d1a;
}

/* Garden Card Styles for About and Updates */
.grow-a-garden-about,
.grow-a-garden-updates {
  background: #fffbe6;
  border: 2px solid #a3d977;
  border-radius: 18px;
  box-shadow: 0 4px 16px rgba(163, 217, 119, 0.15);
  padding: 2rem 2.5rem;
  margin: 2rem 0;
  /* max-width: 80%; */
  width: 95%;
  color: #3d5229;
  position: relative;
  text-align: left;
}

.grow-a-garden-about {
  margin-top: 40px;
  margin-bottom: 24px;
  border-left: 8px solid #7ed957;
  background: linear-gradient(120deg, #f5fff0 80%, #e0ffe0 100%);
}

.grow-a-garden-updates {
  margin-bottom: 40px;
  border-left: 8px solid #ffd966;
  background: linear-gradient(120deg, #f0fff7 80%, #fffbe6 100%);
}

.grow-a-garden-about h2,
.grow-a-garden-updates h2 {
  color: #5a8c3e;
  margin-bottom: 1rem;
  font-family: 'Montserrat', 'Segoe UI', Arial, sans-serif;
  letter-spacing: 1px;
}

.grow-a-garden-about ul,
.grow-a-garden-updates ul {
  list-style: none;
  padding: 0;
}

.grow-a-garden-about li,
.grow-a-garden-updates li {
  margin: 0.5rem 0;
  padding-left: 1.7em;
  position: relative;
  font-size: 1.08em;
}

.grow-a-garden-about li::before {
  content: "ðŸŒ±";
  position: absolute;
  left: 0;
}

.grow-a-garden-updates li::before {
  content: "ðŸŒ»";
  position: absolute;
  left: 0;
}

.grow-a-garden-about strong,
.grow-a-garden-updates strong {
  color: #388e3c;
}

/* Links */
a {
  color: #4e7d2c;
  text-decoration: underline;
  transition: color 0.2s;
}

a:hover {
  color: #2e4d1a;
}

/* Footer Styles */
.footer {
  background: #d2f5c4;
  color: #3d5229;
  text-align: center;
  padding: 1.5rem 0 1rem 0;
  border-top: 2px solid #a3d977;
  margin-top: auto;
  font-size: 1rem;
}

.footer a {
  color: #5a8c3e;
  font-weight: bold;
}

.footer a:hover {
  color: #2e4d1a;
}

/* Responsive Design */
@media (max-width: 767px), (max-height: 480px) {
  .footer {
    display: none;
  }
  .grow-a-garden-about,
  .grow-a-garden-updates {
    padding: 1rem 0.5rem;
    max-width: 98vw;
  }
}

@media (min-width: 600px) {
  .app {
    padding: 40px 20px 20px 20px;
  }
  .grow-a-garden-about,
  .grow-a-garden-updates {
    padding: 2.5rem 3rem;
    max-width: 700px;
  }
  .footer {
    padding: 2rem 0 1.5rem 0;
  }
}

@media (min-width: 1200px) {
  .grow-a-garden-about,
  .grow-a-garden-updates {
    max-width: 800px;
  }
}