.fruits-calculator {
  background: #f0fff7;
  border: 2px solid #a3d977;
  border-radius: 18px;
  box-shadow: 0 4px 16px rgba(163, 217, 119, 0.15);
  padding: 2rem 2.5rem;
  margin: 2rem auto;
  color: #29522e;
  font-family: 'Segoe UI', Arial, sans-serif;
  max-width: 1200px;
}

.fruits-calculator h2 {
  color: #388e3c;
  font-family: 'Montserrat', 'Segoe UI', Arial, sans-serif;
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 0.5rem;
  text-align: center;
}

.fruits-calculator > .fruit-calc-note {
  margin-top: 1.5em;
  color: #388e3c;
  font-size: 0.98em;
  text-align: center;
}

.fruit-calc-form {
  display: flex;
  flex-direction: column;
  gap: 1.2em;
  margin-bottom: 1.5em;
  align-items: center;
}

.fruit-calc-form label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-weight: 600;
  color: #388e3c;
  width: 100%;
  max-width: 400px;
}

.fruit-calc-form input,
.fruit-calc-form select {
  margin-top: 0.3em;
  padding: 0.7em 1em;
  border-radius: 8px;
  border: 2px solid #b6e388;
  font-size: 1em;
  width: 100%;
  box-sizing: border-box;
  outline: none;
  transition: border-color 0.3s;
}

.fruit-calc-form input:focus,
.fruit-calc-form select:focus {
  border-color: #7ed957;
}

.fruit-calc-form button {
  background: #a3d977;
  color: #29522e;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  padding: 0.7em 1.5em;
  font-size: 1.1em;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

.fruit-calc-form button:hover {
  background: #7ed957;
  box-shadow: 0 4px 8px rgba(163, 217, 119, 0.15);
}

.fruit-calc-result {
  background: #fffbe6;
  border: 1px solid #ffd966;
  border-radius: 10px;
  padding: 1em;
  margin-top: 1em;
  font-size: 1.2em;
  color: #388e3c;
  text-align: center;
}

.mutation-table-details {
  margin-top: 1.5em;
  text-align: left;
}

.mutation-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 0.7em;
  background: #fffbe6;
  font-size: 0.98em;
}

.mutation-table th,
.mutation-table td {
  border: 1px solid #b6e388;
  padding: 0.5em 0.7em;
  text-align: left;
}

.mutation-table th {
  background: #e0ffe0;
}

.mutation-fieldset {
  border: 1px solid #b6e388;
  border-radius: 10px;
  padding: 1em;
  margin-top: 1em;
  background: #f8fff4;
  width: 100%;
  max-width: 900px;
}

.mutation-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  gap: 0.5em 1.5em;
}

.mutation-checkbox {
  display: flex;
  align-items: center;
  gap: 0.5em;
  font-weight: 500;
  padding: 0.2em 0.1em;
  cursor: pointer;
  transition: background 0.15s;
  border-radius: 6px;
}

.mutation-checkbox:hover {
  background: #e0ffe0;
}

.mutation-checkbox input[type="checkbox"] {
  accent-color: #7ed957;
  width: 1.1em;
  height: 1.1em;
  margin: 0;
}

.mutation-label {
  flex: 1;
  font-size: 1em;
  color: #29522e;
}

/* Responsive styles */
@media (max-width: 900px) {
  .fruits-calculator {
    padding: 1.5rem;
  }
  .mutation-fieldset {
    max-width: 100%;
  }
}

@media (max-width: 600px) {
  .fruits-calculator {
    padding: 1rem;
  }
  .fruit-calc-form label {
    max-width: 100%;
  }
  .mutation-grid {
    grid-template-columns: 1fr;
  }
}