.grow-a-garden-about {
  background: #f5fff0;
  border: 2px solid #b6e388;
  border-radius: 18px;
  box-shadow: 0 4px 16px rgba(163, 217, 119, 0.15);
  padding: 2rem 2.5rem;
  margin: 2rem auto;
  /* max-width: 600px; */
  color: #3d5229;
  font-family: 'Segoe UI', Arial, sans-serif;
}

.grow-a-garden-about h2 {
  color: #388e3c;
  font-family: 'Montserrat', 'Segoe UI', Arial, sans-serif;
  font-size: 2.2em;
  font-weight: 700;
  margin-bottom: 1rem;
  letter-spacing: 0.5px;
}

.grow-a-garden-about ul {
  list-style: none;
  padding: 0;
}

.grow-a-garden-about li {
  margin: 0.5rem 0;
  padding-left: 1.5em;
  position: relative;
}

